import { differenceInMilliseconds } from 'date-fns';
import React, { useMemo, useRef } from 'react';

import { Appointment, AppointmentsBlock } from '../../../../../../stores/appointments';
import AppointmentWrapper from '../AppointmentWrapper';

export interface AppointmentBlockProps {
  appsBlock: AppointmentsBlock;
  rowHeight: number;
  columnWidth: number;
  zeroAxis: Date | number;
  gridStepDuration: Date | number;
}

function generateApp({
  app,
  rowHeight,
  columnWidth,
  zeroAxis,
  gridStepDuration
}: {
  app: Appointment;
  rowHeight: number;
  columnWidth: number;
  zeroAxis: number | Date;
  gridStepDuration: number | Date;
}): JSX.Element {
  const appDuration = app.interval.end - app.interval.start;
  // console.log(appDuration);

  const height = rowHeight;
  const width = columnWidth * (appDuration / gridStepDuration.valueOf());
  const x =
    (differenceInMilliseconds(app.interval.start, zeroAxis) /
      gridStepDuration.valueOf()) *
    columnWidth;
  const y = app.rowIndex * rowHeight;

  return (
    <AppointmentWrapper
      key={app.id}
      height={height}
      width={width}
      left={x}
      top={y}
      app={app}
    />
  );
}

// const AppointmentBlockInternal =

const AppointmentBlock = React.memo(
  ({
    appsBlock,
    rowHeight,
    gridStepDuration,
    columnWidth,
    zeroAxis
  }: AppointmentBlockProps) => {
    console.log("app block updated");
    const cachedApps = useRef<Record<string, JSX.Element>>({});

    const cachedByEnvironment = useMemo(
      () =>
        appsBlock.elements.reduce(
          (acc: Record<string, JSX.Element>, app) => ({
            ...acc,
            [app.id]: generateApp({
              app,
              columnWidth,
              gridStepDuration,
              rowHeight,
              zeroAxis
            })
          }),
          {}
        ),
      [rowHeight, gridStepDuration, columnWidth, zeroAxis, appsBlock.elements]
    );

    const appElements = appsBlock.elements.reduce(
      (acc: Record<string, JSX.Element>, app) => ({
        ...acc,
        [app.id]:
          cachedByEnvironment[app.id] ||
          cachedApps.current[app.id] ||
          generateApp({
            app,
            columnWidth,
            gridStepDuration,
            rowHeight,
            zeroAxis
          })
      }),
      {}
    );

    // tslint:disable-next-line:no-object-mutation
    cachedApps.current = appElements;

    return <>{Object.values(appElements)}</>;
  }
);

export default AppointmentBlock;
