import { addMilliseconds, addMinutes, isEqual } from 'date-fns';
import { useMemo, useState } from 'react';
import { createContainer } from 'unstated-next';
import { useMedia, usePrevious } from 'use-hooks';

import { Appointment } from '../../stores/appointments';
import { UIInfo } from '../../stores/ui';
import styleVariables from './Calendar.variables.scss';

console.log(styleVariables);

function useLeftColumn() {
  const [leftColumnWidth, setLeftColumnWidth] = useState(50);
  return { leftColumnWidth, setLeftColumnWidth };
}

function useColumns() {
  const [columnWidth, setColumnWidth] = useState(0);
  const [columnsCount, setColumnsCount] = useState(0);

  const setColumns = (containerWidth: number, minColumnWidth: number) => {
    const newColumnsCount = Math.floor(containerWidth / minColumnWidth);
    const newColumnWidth = containerWidth / newColumnsCount;

    setColumnsCount(newColumnsCount);
    setColumnWidth(newColumnWidth);
  };

  return { columnWidth, columnsCount, setColumns };
}

function useRows() {
  const [rowHeight, setRowHeight] = useState(
    parseFloat(styleVariables.rowHeight)
  );
  const [rowsCount, setRowsCount] = useState(40);

  const setRows = (newRowsCount: number, newRowHeight: number) => {
    setRowsCount(newRowsCount);
    setRowHeight(newRowHeight);
  };

  return { rowHeight, rowsCount, setRows };
}

function useTimeLine() {
  const { rowHeight } = RowsInfo.useContainer();

  const [timeLineHeight, setTimeLineHeight] = useState(rowHeight);

  return { timeLineHeight, setTimeLineHeight };
}

function useScroll() {
  const { columnWidth } = ColumnsInfo.useContainer();

  const [scrollOffset, setScrollOffset] = useState(0);
  const oneScrollSize = useMedia(["(max-width: 800px)"], [1], 0.4);
  const [isScrolling, setIsScrolling] = useState(false);
  const [scrollDuration] = useState(0.5);
  const [oneScrollSizePixels, setOneScrollSizePixels] = useState(0);
  const [oneScrollSizeReal, setOneScrollSizeReal] = useState(0);
  const [lastScrollDirection, setLastScrollDirection] = useState(0);

  const [scrollOffsetDone, setScrollOffsetDone] = useState(scrollOffset);

  const columnsPerScroll = Math.round(oneScrollSizePixels / columnWidth) || 0;

  const scrollingEventsGenerator = useMemo(
    () =>
      (function*(): Iterator<
        { justEnded: boolean; justStarted: boolean },
        { justEnded: boolean; justStarted: boolean },
        boolean
      > {
        let wasScrolling = false;
        let justEnded = false;
        let justStarted = false;
        let currentScrolling = false;

        while (true) {
          currentScrolling = yield {
            justEnded,
            justStarted
          };

          justStarted = currentScrolling && !wasScrolling;
          justEnded = !currentScrolling && wasScrolling;

          wasScrolling = currentScrolling;
        }
      })(),
    []
  );

  const {
    justStarted: scrollingJustStarted,
    justEnded: scrollingJustEnded
  } = scrollingEventsGenerator.next(isScrolling).value;

  const pageWidth = oneScrollSizePixels / oneScrollSizeReal;

  const scrollRight = () => {
    setScrollOffset(scrollOffset - 1);
    setIsScrolling(true);
    setLastScrollDirection(1);
  };

  const scrollLeft = () => {
    setScrollOffset(scrollOffset + 1);
    setIsScrolling(true);
    setLastScrollDirection(-1);
  };

  const endScroll = () => {
    setIsScrolling(false);
    setScrollOffsetDone(scrollOffset);
  };

  return {
    endScroll,
    isScrolling,
    oneScrollSize,
    oneScrollSizePixels,
    oneScrollSizeReal,
    pageWidth,
    scrollDuration,
    scrollingJustEnded,
    scrollingJustStarted,
    scrollLeft,
    columnsPerScroll,
    scrollOffset,
    scrollRight,
    setOneScrollSizePixels,
    setOneScrollSizeReal,
    lastScrollDirection,
    scrollOffsetDone
  };
}

function useGrid() {
  const [zeroAxis, setZeroAxis] = useState<Date | number>(0);
  const [gridStepDuration, setGridStepDuration] = useState(addMinutes(0, 40));

  return {
    gridStepDuration,
    setGridStepDuration,
    setZeroAxis,
    zeroAxis
  };
}

function useViewInterval() {
  const { zeroAxis, gridStepDuration } = GridInfo.useContainer();
  const {
    scrollOffset,
    oneScrollSizeReal,
    pageWidth,
    isScrolling
  } = ScrollInfo.useContainer();
  const { columnWidth } = ColumnsInfo.useContainer();
  const { pageLoaded } = UIInfo.useContainer();
  const pageLoadedPrevious = (usePrevious(pageLoaded) as any) as boolean;
  const pageFirstLoaded = pageLoaded !== pageLoadedPrevious;

  const isScrollingPrevious = (usePrevious(isScrolling) as any) as boolean;
  const pageScrollingEnded = isScrollingPrevious && !isScrolling;

  const onePageDuration =
    Math.round(pageWidth / columnWidth) * gridStepDuration.valueOf() || 0;
  const oneScrollDuration = onePageDuration * oneScrollSizeReal;

  const viewInterval: Interval = {
    end: addMilliseconds(
      zeroAxis,
      -scrollOffset * oneScrollDuration + onePageDuration
    ),
    start: addMilliseconds(zeroAxis, -scrollOffset * oneScrollDuration)
  };

  const [viewIntervalAfterScrolling, setViewIntervalAfterScrolling] = useState(
    viewInterval
  );

  if (
    (pageFirstLoaded || pageScrollingEnded) &&
    (!isEqual(viewIntervalAfterScrolling.start, viewInterval.start) ||
      !isEqual(viewIntervalAfterScrolling.end, viewInterval.end))
  )
    setViewIntervalAfterScrolling(viewInterval);

  return {
    onePageDuration,
    oneScrollDuration,
    viewInterval,
    viewIntervalAfterScrolling
  };
}

function useDragged() {
  const [draggingApp, setDraggingApp] = useState<Appointment | null>(null);
  return { draggingApp, setDraggingApp };
}

export const DraggedInfo = createContainer(useDragged);
export const TimeLineInfo = createContainer(useTimeLine);
export const ColumnsInfo = createContainer(useColumns);
export const RowsInfo = createContainer(useRows);
export const ScrollInfo = createContainer(useScroll);
export const GridInfo = createContainer(useGrid);
export const ViewIntervalInfo = createContainer(useViewInterval);
export const LeftColumnInfo = createContainer(useLeftColumn);
