import './TimeLine.scss';

import { addMilliseconds, format } from 'date-fns';
import React, { useMemo } from 'react';

import CenteredVertically from '../../../../../style-components/CenteredVertically';
import { ColumnsInfo, GridInfo, ScrollInfo, TimeLineInfo, ViewIntervalInfo } from '../../../Calendar.containers';

const TimeLine = () => {
  const { timeLineHeight } = TimeLineInfo.useContainer();
  const { columnsCount, columnWidth } = ColumnsInfo.useContainer();
  const { gridStepDuration } = GridInfo.useContainer();
  const { oneScrollDuration } = ViewIntervalInfo.useContainer();
  const {
    scrollOffsetDone,
    columnsPerScroll,
    oneScrollSizePixels
  } = ScrollInfo.useContainer();

  const columnsCountBuffered = columnsCount + columnsPerScroll * 2;
  return useMemo(
    () => (
      <div
        styleName="timeLine"
        // timeLineHeight={timeLineHeight}
        // columnWidth={columnWidth}
        style={{
          transform: `translateX(${(-scrollOffsetDone - 1) *
            oneScrollSizePixels}px)`,
          height: timeLineHeight
        }}
      >
        {new Array(columnsCountBuffered).fill(null).map((_, i) => (
          <span key={i} styleName="timeMark" style={{ width: columnWidth }}>
            <CenteredVertically>
              {format(
                addMilliseconds(
                  0,
                  gridStepDuration.valueOf() *
                    (i - scrollOffsetDone * columnsPerScroll) -
                    oneScrollDuration || 0
                ),
                "h:mm"
              )}
            </CenteredVertically>
          </span>
        ))}
      </div>
    ),
    [
      scrollOffsetDone,
      columnsCountBuffered,
      gridStepDuration,
      timeLineHeight,
      columnWidth,
      oneScrollDuration,
      oneScrollSizePixels,
      columnsPerScroll
    ]
  );
};

export default TimeLine;
