import { addDays, addMinutes, areIntervalsOverlapping, differenceInMilliseconds } from 'date-fns';
import uuid from 'uuid';

import { randomInt } from '../assembly/random';
import { timeout } from '../assembly/timeout';
import { shell } from '../assembly/utility';
import { Appointment, createAppointment } from '../stores/appointments';

export async function fetchAppointments(
  interval: Interval,
  stepDuration: Date | number = 1,
  densityPerDay = 200,
  maxRowIndex = 10
): Promise<Appointment[]> {
  const delay = 0;
  const possibleDuration: Interval = {
    end: addMinutes(0, 120),
    start: addMinutes(0, 40)
  };

  await timeout(delay);

  function snapValue(val: number): number {
    return Math.floor(val / stepDuration.valueOf()) * stepDuration.valueOf();
  }

  const toGenerate = Math.round(
    differenceInMilliseconds(interval.end, interval.start) *
      (densityPerDay / addDays(0, 1).valueOf())
  );

  const apps = shell(toGenerate)
    .map(() =>
      snapValue(
        randomInt(
          interval.start,
          Math.max(
            interval.start.valueOf(),
            interval.end.valueOf() - possibleDuration.start.valueOf()
          )
        )
      )
    )
    .map(start => {
      const duration = snapValue(
        randomInt(possibleDuration.start, possibleDuration.end)
      );
      const end = Math.min(start + duration, interval.end.valueOf());

      return createAppointment({
        interval: { start, end },
        rowIndex: randomInt(0, maxRowIndex),
        userId: uuid.v4()
      });
    })
    .reduce(
      (acc: Appointment[], app) =>
        acc.some(
          app2 =>
            app2.rowIndex === app.rowIndex &&
            areIntervalsOverlapping(app.interval, app2.interval)
        )
          ? acc
          : [...acc, app],
      []
    );

  console.log(`${apps.length} apps fetched`);

  return apps;
}

export default 0;
