import './BackGrid.scss';

import React, { useMemo, useState } from 'react';

import { ColumnsInfo, RowsInfo, ScrollInfo } from '../../../Calendar.containers';

const BackGrid = () => {
  const { columnWidth, columnsCount } = ColumnsInfo.useContainer();
  const { rowsCount, rowHeight } = RowsInfo.useContainer();
  const {
    oneScrollSizePixels,
    pageWidth,
    scrollOffset,
    isScrolling,
    scrollOffsetDone
  } = ScrollInfo.useContainer();

  // useWhyDidYouUpdate("BackGrid", {
  //   columnWidth,
  //   columnsCount,
  //   rowsCount,
  //   rowHeight,
  //   oneScrollSizePixels,
  //   pageWidth,
  //   scrollingJustStarted,
  //   scrollingJustEnded
  // });

  const buffer = oneScrollSizePixels;
  const additionalColumnsOneSide = Math.ceil(buffer / columnWidth) || 0;
  const columns = useMemo(
    () =>
      new Array(columnsCount + additionalColumnsOneSide * 2 + rowsCount + 2)
        .fill(null)
        .map((_, i) =>
          i <= rowsCount + 1 ? (
            <div
              key={i}
              styleName="border horizontal"
              style={{
                height: rowHeight,
                transform: `translate(${-buffer}px, ${i * rowHeight}px)`,
                width: buffer * 2 + pageWidth || 0
              }}
            />
          ) : (
            <div
              key={i}
              styleName="border vertical"
              style={{
                transform: `translate(${(i -
                  additionalColumnsOneSide -
                  rowsCount -
                  1) *
                  columnWidth}px, 0px)`,
                width: columnWidth
              }}
            />
          )
        ),
    [
      columnsCount,
      pageWidth,
      rowsCount,
      buffer,
      additionalColumnsOneSide,
      rowHeight,
      columnWidth
    ]
  );

  const translateX =
    -(isScrolling ? scrollOffsetDone : scrollOffset) * oneScrollSizePixels;
  const translate = `translate(${translateX}px, ${0}px)`;

  return useMemo(
    () => (
      <div styleName="backGrid" style={{ transform: translate }}>
        {columns}
      </div>
    ),
    [translate, columns]
  );
};

export default BackGrid;
