import './Calendar.scss';

import { addHours } from 'date-fns';
import React, { useEffect, useMemo } from 'react';
import { useSwipeable } from 'react-swipeable';
import { useKeyPress } from 'use-hooks';

import { fetchAppointments } from '../../fetchers/fetchAppointments';
import { AppointmentsInfo } from '../../stores/appointments';
import { UIInfo } from '../../stores/ui';
import { UsersInfo } from '../../stores/users';
import {
  ColumnsInfo,
  DraggedInfo,
  GridInfo,
  LeftColumnInfo,
  RowsInfo,
  ScrollInfo,
  TimeLineInfo,
  ViewIntervalInfo,
} from './Calendar.containers';
import LeftBlock from './LeftBlock';
import MainBlock from './MainBlock';
import TopBlock from './TopBlock';

const Calendar = () => {
  const { isScrolling, scrollLeft, scrollRight } = ScrollInfo.useContainer();
  const { addAppointments } = AppointmentsInfo.useContainer();
  const { gridStepDuration } = GridInfo.useContainer();
  const { setPageLoaded } = UIInfo.useContainer();
  const { rowsCount } = RowsInfo.useContainer();

  const arrowRightPressed = useKeyPress("ArrowRight", true);
  const arrowLeftPressed = useKeyPress("ArrowLeft", true);

  const handlers = useSwipeable({
    onSwipedLeft: () => !isScrolling && scrollRight(),
    onSwipedRight: () => !isScrolling && scrollLeft()
  });

  if (!isScrolling)
    if (arrowRightPressed) scrollRight();
    else if (arrowLeftPressed) scrollLeft();

  useEffect(() => {
    (async () => {
      addAppointments(
        await fetchAppointments(
          { start: addHours(0, -65), end: addHours(0, 145) },
          // { start: addHours(0, -2), end: addHours(0, 3) },
          gridStepDuration,
          500,
          rowsCount
        )
      );
      setPageLoaded(true);
    })();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return useMemo(
    () => (
      <div styleName="calendar" {...handlers}>
        <div styleName="top">
          <TopBlock />
        </div>
        <div styleName="bottom">
          <LeftBlock />
          <GridInfo.Provider>
            <ViewIntervalInfo.Provider>
              <TimeLineInfo.Provider>
                <MainBlock minColumnWidth={200} />
              </TimeLineInfo.Provider>
            </ViewIntervalInfo.Provider>
          </GridInfo.Provider>
        </div>
      </div>
    ),
    [handlers]
  );
};

const Wrapper = () => {
  return (
    <UsersInfo.Provider>
      <DraggedInfo.Provider>
        <AppointmentsInfo.Provider initialState={20}>
          <ColumnsInfo.Provider>
            <ScrollInfo.Provider>
              <GridInfo.Provider>
                <RowsInfo.Provider>
                  <LeftColumnInfo.Provider>
                    <Calendar />
                  </LeftColumnInfo.Provider>
                </RowsInfo.Provider>
              </GridInfo.Provider>
            </ScrollInfo.Provider>
          </ColumnsInfo.Provider>
        </AppointmentsInfo.Provider>
      </DraggedInfo.Provider>
    </UsersInfo.Provider>
  );
};

export default Wrapper;
