import { List } from 'immutable';
import { useState } from 'react';
import { createContainer } from 'unstated-next';
import uuid from 'uuid';

import { IntervalInt, withinInterval } from '../assembly/interval';

function maxInterval(i1: IntervalInt, i2: IntervalInt): IntervalInt {
  return {
    end: Math.max(i1.end, i2.end),
    start: Math.min(i1.start, i2.start)
  };
}

export interface Appointment {
  id: string;
  userId: string;
  interval: IntervalInt;
  rowIndex: number;
}

export interface AppointmentsBlock {
  id: string;
  interval: IntervalInt;
  size: number;
  elements: List<Appointment>;
}

export function createAppointment(
  args: Partial<Appointment> & Omit<Appointment, "id">
): Appointment {
  return {
    ...args,
    id: args.id || uuid.v4()
  };
}

function useAppointments(initialState = 25) {
  const [blockSize] = useState(initialState);

  const [appointmentBlocks, setAppointmentBlocks] = useState<
    List<AppointmentsBlock>
  >(List.of<AppointmentsBlock>());

  const addAppointment = (app: Appointment) => {
    const blockIndex = appointmentBlocks.findLastIndex(
      block =>
        block.elements.size <= blockSize &&
        (block.interval.start.valueOf() <= app.interval.start.valueOf() ||
          block.interval.start === Infinity)
    );

    const targetBlock = appointmentBlocks.get(blockIndex) as AppointmentsBlock;
    const newApps =
      blockIndex === -1
        ? appointmentBlocks.push({
            elements: List.of(app),
            id: uuid.v4(),
            interval: app.interval,
            size: 1
          })
        : appointmentBlocks.set(blockIndex, {
            elements: targetBlock.elements
              .push(app)
              .sortBy(a => a.interval.start),
            id: targetBlock.id,
            interval: maxInterval(targetBlock.interval, app.interval),
            size: targetBlock.size + 1
          });

    setAppointmentBlocks(newApps);
  };

  const addAppointments = (apps: Appointment[]) => {
    const blockIndex2apps: Record<number, Appointment[]> = apps.reduce(
      (acc: Record<number, Appointment[]>, app) => {
        const blockIndex = appointmentBlocks.findLastIndex(
          (block, i) =>
            block.elements.size + (acc[i] || { length: 0 }).length <
              blockSize &&
            block.interval.start.valueOf() <= app.interval.start.valueOf()
        );

        return { ...acc, [blockIndex]: [...(acc[blockIndex] || []), app] };
      },
      {}
    );

    const appsWithoutBlock = blockIndex2apps[-1] || [];
    const newBlocks = Array.from(
      List(appsWithoutBlock)
        .sortBy(a => a.interval.start)
        .reduce((acc: AppointmentsBlock[], val: Appointment) => {
          if (!acc.length)
            return [
              {
                elements: List.of(val),
                id: uuid.v4(),
                interval: val.interval,
                size: 1
              }
            ];

          const currentBlockIndex =
            acc[acc.length - 1].size < blockSize ? acc.length - 1 : acc.length;
          const currentBlock = acc[currentBlockIndex] || {
            elements: List(),
            id: uuid.v4(),
            interval: {
              end: -Infinity,
              start: Infinity
            },
            size: 0
          };

          return [
            ...acc.slice(0, currentBlockIndex),
            {
              elements: currentBlock.elements.push(val),
              id: currentBlock.id,
              interval: maxInterval(currentBlock.interval, val.interval),
              size: currentBlock.size + 1
            }
          ];
        }, [])
    );

    const newAppBlocks: List<AppointmentsBlock> = appointmentBlocks.map(
      (block, i) =>
        i in blockIndex2apps
          ? {
              ...block,
              elements: block.elements
                .concat(blockIndex2apps[i])
                .sortBy(({ interval }) => interval.start),
              id: block.id,
              size: block.size + blockIndex2apps[i].length
            }
          : block
    );

    setAppointmentBlocks(
      newAppBlocks.concat(newBlocks).sortBy(b => b.interval.start)
    );
  };

  const getAppCoords = (id: string) => {
    const coords: {
      blockId: number;
      appIndex: number;
    } | null = appointmentBlocks.reduce((coords, block, blockId) => {
      if (coords) return coords;

      const appIndex = block.elements.findIndex(app => app.id === id);
      if (appIndex === -1) return coords;

      return { blockId, appIndex };
    }, null);

    return coords;
  };

  const updateApp = (newApp: Appointment) => {
    const { blockId, appIndex } = getAppCoords(newApp.id);
    const block: AppointmentsBlock = appointmentBlocks.get(blockId);
    const updatedBlock: AppointmentsBlock = {
      ...block,
      interval: maxInterval(block.interval, newApp.interval),
      elements: block.elements.update(appIndex, () => newApp)
    };

    const previousApp = block.elements.get(appIndex);
    const newAppointmentBlocks = appointmentBlocks.update(
      blockId,
      () => updatedBlock
    );
    console.log(
      `${newApp.id} from ${block.id} was updated (${previousApp.interval.start}, ${previousApp.rowIndex}) -> (${newApp.interval.start}, ${newApp.rowIndex})`
    );
    setAppointmentBlocks(newAppointmentBlocks);
  };

  const getApp = (date: Date | number, row: number) => {
    const blocks = appointmentBlocks.filter(block =>
      withinInterval(date, block.interval)
    );
    const app = blocks.reduce(
      (app: Appointment | null, block) =>
        app
          ? app
          : block.elements.find(
              app =>
                app.rowIndex === row &&
                withinInterval(date, app.interval, true, false)
            ),
      null
    );

    return app;
  };

  return {
    appointmentBlocks,
    addAppointment,
    addAppointments,
    blockSize,
    getApp,
    getAppCoords,
    updateApp
  };
}

export const AppointmentsInfo = createContainer(useAppointments);
