import useInterval from '@use-it/interval';
import React, { useCallback, useMemo, useRef, useState } from 'react';
import { createContainer } from 'unstated-next';

import { AppointmentsBlock, AppointmentsInfo } from '../../../../../stores/appointments';
import { LazyTaskContainer } from '../../../../../stores/lazytask';
import { ColumnsInfo, GridInfo, RowsInfo, ViewIntervalInfo } from '../../../Calendar.containers';
import AppointmentBlock from './AppointmentBlock';
import { AppointmentBlockProps } from './AppointmentBlock/AppointmentBlock';

// tslint:disable
const useIsChanged = (dependencies: any[]) => {
  const ref = useRef(dependencies);

  const changed = ref.current.some((v, i) => v !== dependencies[i]);
  ref.current = dependencies;

  return changed;
};
// tslint:enable

function useAppBlocksJSX() {
  const appBlocksRef = useRef<Record<string, JSX.Element>>({});
  const [appBlocks, setAppBlocks] = useState<JSX.Element[]>([]);
  const appBlocksDebounced = appBlocks; // useDebounce(appBlocks, 20);
  const renderedBlocksRef = useRef<Record<string, AppointmentsBlock>>({});

  const addBlock = useCallback(
    (blockJSX: JSX.Element, id: string, block: AppointmentsBlock) => {
      // tslint:disable-next-line:no-object-mutation
      appBlocksRef.current[id] = blockJSX;
      // tslint:disable-next-line:no-object-mutation
      renderedBlocksRef.current[id] = block;

      setAppBlocks(Object.values(appBlocksRef.current));
    },
    []
  );

  const addBlocks = useCallback((arr: Array<[JSX.Element, string]>) => {
    arr.forEach(
      ([blockJSX, id]) =>
        // tslint:disable-next-line:no-object-mutation
        (appBlocksRef.current[id] = blockJSX)
    );

    setAppBlocks(Object.values(appBlocksRef.current));
  }, []);

  const cleanFarBlocks = useCallback(
    (viewInterval: Interval, clearDistanceScale = 1) => {
      const clearDistance =
        (viewInterval.end.valueOf() - viewInterval.start.valueOf()) *
        clearDistanceScale;
      const amountWas = Object.keys(appBlocksRef.current).length;
      // tslint:disable-next-line:no-object-mutation
      appBlocksRef.current = Object.entries(appBlocksRef.current)
        .filter(([, jsx]) => {
          const { interval } = (jsx.props as AppointmentBlockProps).appsBlock;
          const dists = [
            viewInterval.start.valueOf() - interval.start,
            viewInterval.end.valueOf() - interval.start,
            viewInterval.start.valueOf() - interval.start,
            viewInterval.end.valueOf() - interval.end
          ].map(d => Math.abs(d));

          const minDist = dists.reduce((min, d) => Math.min(min, d));
          return minDist < clearDistance;
        })
        .reduce(
          (acc: Record<string, JSX.Element>, [key, jsx]) => ({
            ...acc,
            [key]: jsx
          }),
          {}
        );

      const amountNow = Object.keys(appBlocksRef.current).length;

      if (amountWas !== amountNow) {
        setAppBlocks(Object.values(appBlocksRef.current));
        console.log(`${amountWas} => ${amountNow} (clear)`);
      }
    },
    []
  );

  return {
    appBlocks: appBlocksDebounced,
    addBlock,
    addBlocks,
    cleanFarBlocks,
    renderedBlocks: renderedBlocksRef.current
  };
}

const AppBlocksJSXInfo = createContainer(useAppBlocksJSX);

const AppointmentsPlacer = () => {
  const { columnWidth } = ColumnsInfo.useContainer();
  const { rowHeight } = RowsInfo.useContainer();
  const { appointmentBlocks } = AppointmentsInfo.useContainer();
  const { zeroAxis, gridStepDuration } = GridInfo.useContainer();
  const {
    viewIntervalAfterScrolling: viewInterval,
    oneScrollDuration
  } = ViewIntervalInfo.useContainer();
  const {
    addLazyTask,
    registerPostEffectDuration
  } = LazyTaskContainer.useContainer();
  const {
    addBlock,
    appBlocks,
    cleanFarBlocks,
    renderedBlocks
  } = AppBlocksJSXInfo.useContainer();

  // console.log("update");

  // const appBlocksCount = appBlocks.length;
  // const appBlocksCountPrevious = usePrevious(appBlocksCount);

  const currentEnvironment = [
    columnWidth,
    gridStepDuration,
    rowHeight,
    viewInterval,
    zeroAxis
  ];

  const appBlocksInProcessRef = useRef(new Set<string>());
  const appBlocksInProcess = appBlocksInProcessRef.current;

  // console.log(`${appBlocksCountPrevious} => ${appBlocksCount} (render)`);
  // useWhyDidYouUpdate("AppointmentPlacer", {
  //   columnWidth,
  //   rowHeight,
  //   appointmentBlocks,
  //   zeroAxis,
  //   gridStepDuration,
  //   viewInterval,
  //   oneScrollDuration,
  //   addLazyTask,
  //   addBlock,
  //   appBlocks,
  //   cleanFarBlocks,
  //   appBlocksCountPrevious
  // });

  const environmentChanged = useIsChanged(currentEnvironment);

  const newAppBlocks = !environmentChanged
    ? appointmentBlocks
        .filter(block => !appBlocksInProcess.has(block.id))
        .filter(
          block =>
            !renderedBlocks[block.id] || renderedBlocks[block.id] !== block
        )
    : appointmentBlocks;

  const appBlocksInRange = newAppBlocks.filter(block => {
    const s1 = block.interval.start;
    const e1 = block.interval.end;
    const s2 = viewInterval.start.valueOf() - oneScrollDuration;
    const e2 = viewInterval.end.valueOf() + oneScrollDuration;

    return (s1 > s2 && s1 < e2) || (e1 > s2 && e1 < e2);
  });

  console.log(renderedBlocks);
  console.log(Object.values(appBlocksInProcess.values()));
  console.log(
    `${appBlocksInRange.count()}/${newAppBlocks.count()}, ${appointmentBlocks
      .filter(
        block => renderedBlocks[block.id] && renderedBlocks[block.id] !== block
      )
      .count()}`
  );

  if (appBlocksInRange.count()) {
    appBlocksInRange.forEach(({ id }) => appBlocksInProcess.add(id));

    // lazy-lazy code
    appBlocksInRange.forEach(block =>
      addLazyTask({
        callback: () => {
          const t1 = Date.now();
          performance.mark("blockJSX generating - start");

          const blockJSX = (
            <AppointmentBlock
              columnWidth={columnWidth}
              key={block.id}
              appsBlock={block}
              gridStepDuration={gridStepDuration}
              rowHeight={rowHeight}
              zeroAxis={zeroAxis}
            />
          );
          performance.mark("blockJSX generating - end");
          performance.measure(
            "blockJSX generating",
            "blockJSX generating - start",
            "blockJSX generating - end"
          );

          performance.mark("addBlock - start");
          addBlock(blockJSX, block.id, block);
          appBlocksInProcess.delete(block.id);

          const diff = Date.now() - t1;

          registerPostEffectDuration("AppointBlock JSX Generation", diff, 1);

          performance.mark("addBlock - end");
          performance.measure("addBlock", "addBlock - start", "addBlock - end");
        },
        priority: 3,
        type: "AppointBlock JSX Generation"
      })
    );

    // lazy-sync code
    // const promises = appBlocksInRange.map(block =>
    //   addLazyTask({
    //     callback: () => {
    //       const blockJSX = (
    //         <AppointmentBlock
    //           columnWidth={columnWidth}
    //           key={block.id}
    //           appsBlock={block}
    //           gridStepDuration={gridStepDuration}
    //           rowHeight={rowHeight}
    //           zeroAxis={zeroAxis}
    //         />
    //       );

    //       return [blockJSX, block.id] as [JSX.Element, string];
    //     },
    //     priority: 3,
    //     type: "AppointBlock JSX Generation"
    //   })
    // );

    // Promise.all(promises).then((arr: Array<[JSX.Element, string]>) =>
    //   addBlocks(arr)
    // );

    // sync code
    // appBlocksInRange.forEach(block => {
    //   const blockJSX = (
    //     <AppointmentBlock
    //       columnWidth={columnWidth}
    //       key={block.id}
    //       appsBlock={block}
    //       gridStepDuration={gridStepDuration}
    //       rowHeight={rowHeight}
    //       zeroAxis={zeroAxis}
    //     />
    //   );

    //   addBlock(blockJSX, block.id);
    // });
  }

  useInterval(
    () =>
      addLazyTask({
        callback: () => cleanFarBlocks(viewInterval),
        priority: 0,
        type: "cleanFarBlocks"
      }),
    533
  );

  return useMemo(() => <>{appBlocks}</>, [appBlocks]);
};

const Wrapper = () => (
  <AppBlocksJSXInfo.Provider>
    <AppointmentsPlacer />
  </AppBlocksJSXInfo.Provider>
);

export default Wrapper;

